<template>
  <el-container>
      <!-- 顶部 -->
    <el-header>
      <div class="Title"><h3>A JIAN 博客系统管理</h3></div>
      <el-button type="info" @click="Back">退出</el-button>
    </el-header>
    <el-container>
        <!-- 菜单栏 -->
      <el-aside :width="status==false?'255px':'64px'">
        <div class="collapse" @click="Collapse">|||</div>
        <el-menu
          class="el-menu-vertical-demo"
          background-color="#545c64"
          text-color="#fff"
          :unique-opened="true"
          :default-active="active"
          :collapse='status'
          :collapse-transition='false'
          :router="true"
        >
          <el-submenu
            :index="item._id"
            v-for="(item, index) in asideArr"
            :key="index"
          >
            <template slot="title">
              <i :class="Icon[item._id]"></i>
              <span style="margin-left: 8px">{{ item.authname }}</span>
            </template>
            <el-menu-item
              :index="'/'+itemS.path"
              v-for="(itemS, index) in item.children"
              :key="index"
              @click="Active('/'+itemS.path)"
            >
              <i class="el-icon-menu"></i>
              <span slot="title">{{ itemS.authname }}</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!-- 内容区域 -->
      <el-main>
          <!-- 占位符 -->
          <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
export default {
  name: 'Home',
  data() {
    return {
      // 菜单列表信息
      asideArr: [],
      // 图标
      Icon: {
        '5fd7653dff63904468d3f238': 'iconfont icon-yonghuming',
        '5fd7654b02d56044f48a2d75': 'iconfont icon-wenzhang-copy',
        '5fd765671f46712918bf7523': 'iconfont icon-yonghuming1',
      },
      //获取菜单栏当前激活状态
      active: '',
      status:false
    }
  },
  created() {
    this.GetAside()
    this.active=this.Storage.Session.get('active')
  },
  methods: {
    // 获取菜单列表
    GetAside() {
      this.$http.get(this.$api.API_SERCER_HOME_ASIDE).then((res) => {
        if (res.data.status != 200) {
          return this.$message.error('获取信息失败')
        }
        this.asideArr = res.data.data
      })
    },
    // 存储当前菜单激活信息
    Active(val) {
      this.active = val
      this.Storage.Session.set('active', val)
    },
    // 实现扇形折叠切换
    Collapse(){
        this.status=!this.status
    },
    // 退出登录
    Back(){
        this.Storage.Session.clear()
        this.$message.success('退出成功')
        this.$router.push('/login')
    }
  },
}
</script>
<style lang="less" scoped>
.el-container {
  height: 100%;
}
.el-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(8, 107, 189, 0.5);
}
.el-aside {
  background-color: #545c64;
  .collapse {
    height: 30px;
    text-align: center;
    line-height: 30px;
    letter-spacing: 0.2rem;
    background-color: #3e74aa;
    cursor: pointer;
    color: #fff;
  }
}
.el-menu {
  border: 0;
}
</style>